@font-face {
  font-family: TTLakes-Bold;
  src: url(./fonts/TTLakes-Bold.ttf); }

@font-face {
  font-family: TTLakes-Medium;
  src: url(./fonts/TTLakes-Medium.ttf); }

@font-face {
  font-family: TTLakes-Light;
  src: url(./fonts/TTLakes-Regular.ttf); }

.body {
  max-width: 1440px;
  flex-direction: row-reverse;
  margin: 0 auto;
  position: relative;
  font-family: 'TTLakes-Light', Arial, serif;
  transition: all 0.3s;
  overflow-x: hidden; }

.page {
  background-color: #f8f8f8;
  background-size: 100%; }

@media (min-width: 1440px) {
  .body {
    display: flex; } }

.swiper {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  z-index: 1; }

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box; }

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform; }

.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px; }

.card {
  position: relative;
  background-color: #ffffff;
  border-radius: 6px;
  border: 1px solid #eaeaea; }

.card:hover {
  cursor: pointer;
  border: 1px solid #41F6D7; }

.swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  background: #DDDDDD; }

.swiper-pagination-bullet-active {
  background: #B5B6BC; }

@media (max-width: 768px) {
  .swiper {
    display: flex;
    width: 320px;
    height: 100%;
    padding-bottom: 32px; }
  .swiper::after {
    content: '';
    position: absolute;
    width: 25px;
    height: 100vh;
    left: 296px;
    z-index: 10;
    background: linear-gradient(90deg, transparent, #f8f8f8); }
  .swiper-wrapper {
    display: flex;
    gap: 0; }
  .swiper-slide {
    width: 240px;
    margin-left: 16px; }
  .card-equipment {
    width: 240px; }
  .slider-3 .swiper-slide {
    width: 260px;
    margin-left: 8px; }
  .content-table {
    display: none; }
  .button-mode {
    display: none; } }

@media (min-width: 768px) {
  .content-card {
    display: none; } }

@media (min-width: 1120px) {
  .grid {
    grid-template-columns: repeat(4, 2fr); }
  .card-brands,
  .card-equipment {
    width: 240px;
    box-sizing: border-box; } }
