@font-face {
  font-family: TTLakes-Bold;
  src: url(./fonts/TTLakes-Bold.ttf); }

@font-face {
  font-family: TTLakes-Medium;
  src: url(./fonts/TTLakes-Medium.ttf); }

@font-face {
  font-family: TTLakes-Light;
  src: url(./fonts/TTLakes-Regular.ttf); }

.body {
  max-width: 1440px;
  flex-direction: row-reverse;
  margin: 0 auto;
  position: relative;
  font-family: 'TTLakes-Light', Arial, serif;
  transition: all 0.3s;
  overflow-x: hidden; }

.page {
  background-color: #f8f8f8;
  background-size: 100%; }

@media (min-width: 1440px) {
  .body {
    display: flex; } }

.card-brands {
  display: flex;
  align-items: center;
  padding-left: 16px;
  margin-left: auto;
  margin-right: auto;
  width: 224px;
  height: 72px; }

.card-brands:before {
  content: '';
  width: 8px;
  height: 8px;
  border-top: 2px solid #ff3e79;
  border-right: 2px solid #ff3e79;
  position: absolute;
  right: 32px;
  top: 32px;
  transform: rotate(45deg); }

.card-brands:after {
  content: '';
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid #ff3e79;
  position: absolute;
  right: 16px;
  top: 16px; }
