@font-face {
  font-family: TTLakes-Bold;
  src: url(./fonts/TTLakes-Bold.ttf); }

@font-face {
  font-family: TTLakes-Medium;
  src: url(./fonts/TTLakes-Medium.ttf); }

@font-face {
  font-family: TTLakes-Light;
  src: url(./fonts/TTLakes-Regular.ttf); }

.body {
  max-width: 1440px;
  flex-direction: row-reverse;
  margin: 0 auto;
  position: relative;
  font-family: 'TTLakes-Light', Arial, serif;
  transition: all 0.3s;
  overflow-x: hidden; }

.page {
  background-color: #f8f8f8;
  background-size: 100%; }

@media (min-width: 1440px) {
  .body {
    display: flex; } }

.contacts__list {
  display: flex;
  padding: 0; }

.address {
  font-style: normal; }

.link--mail {
  display: block;
  font-family: "TTLakes-Medium", Arial, sans-serif;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1B1C21;
  margin-bottom: 12px; }

.link--phone {
  display: block;
  font-family: "TTLakes-Bold", Arial, sans-serif;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.6px;
  color: #1B1C21;
  margin-bottom: 40px; }

.language__list {
  padding: 0;
  margin-bottom: 32px; }

.language__item {
  display: inline-block; }

.link--language {
  font-family: "TTLakes-Medium", Arial, sans-serif;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1B1C21;
  margin-right: 5px; }

.link--language-active {
  color: #7E7E82; }
