@font-face {
  font-family: TTLakes-Bold;
  src: url(./fonts/TTLakes-Bold.ttf); }

@font-face {
  font-family: TTLakes-Medium;
  src: url(./fonts/TTLakes-Medium.ttf); }

@font-face {
  font-family: TTLakes-Light;
  src: url(./fonts/TTLakes-Regular.ttf); }

.body {
  max-width: 1440px;
  flex-direction: row-reverse;
  margin: 0 auto;
  position: relative;
  font-family: 'TTLakes-Light', Arial, serif;
  transition: all 0.3s;
  overflow-x: hidden; }

.page {
  background-color: #f8f8f8;
  background-size: 100%; }

@media (min-width: 1440px) {
  .body {
    display: flex; } }

.card-prices {
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  padding: 16px;
  width: 260px;
  height: 200px; }

.table-prices {
  width: 100%;
  margin: 0 auto;
  font-size: 17px;
  line-height: 18px;
  letter-spacing: 0.2px;
  color: #1B1C21;
  border-collapse: collapse;
  text-align: left; }

.table-item {
  position: relative;
  height: 65px; }

.menu-item {
  background-color: #ffffff; }

.table-item::after {
  content: '';
  width: 95%;
  height: 1px;
  background-color: #eaeaea;
  position: absolute;
  bottom: 0px;
  left: 2.5%; }

.prices-menu,
.menu-text {
  vertical-align: middle;
  padding-left: 16px; }

.button-table {
  margin-top: 16px; }

.prices-title {
  font-size: 14px;
  line-height: 16px;
  color: #7E7E82;
  opacity: 0.72; }

.prices-text {
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.2px;
  color: #1B1C21; }

.button__prices {
  position: absolute;
  bottom: 16px;
  right: 16px; }

.prices-text {
  margin-bottom: 16px; }
