@font-face {
  font-family: TTLakes-Bold;
  src: url(./fonts/TTLakes-Bold.ttf); }

@font-face {
  font-family: TTLakes-Medium;
  src: url(./fonts/TTLakes-Medium.ttf); }

@font-face {
  font-family: TTLakes-Light;
  src: url(./fonts/TTLakes-Regular.ttf); }

.body {
  max-width: 1440px;
  flex-direction: row-reverse;
  margin: 0 auto;
  position: relative;
  font-family: 'TTLakes-Light', Arial, serif;
  transition: all 0.3s;
  overflow-x: hidden; }

.page {
  background-color: #f8f8f8;
  background-size: 100%; }

@media (min-width: 1440px) {
  .body {
    display: flex; } }

.button-style {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid rgba(0, 0, 0, 0);
  background-color: #ff3e79; }

.button:hover {
  cursor: pointer;
  box-shadow: 0px 0px 10px 0 #1B1C21;
  transform: scale(1.2);
  transition: all 0.3s; }

.button-mode {
  position: relative;
  background: #F8F8F8;
  font-family: "TTLakes-Medium", Arial, serif;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1B1C21;
  padding-left: 20px;
  border: none; }

.button-mode::before {
  content: "";
  height: 11px;
  width: 8px;
  left: 0;
  top: 6px;
  position: absolute;
  background-image: url(./static/icon.svg); }

.clickButton {
  margin-top: 24px; }

.clickButton:hover {
  cursor: pointer;
  border-radius: 6px;
  box-shadow: 0px 0px 5px 0 #1B1C21;
  transition: all 0.3s; }

.close-button::before {
  transform: rotate(180deg); }

.button-action {
  font-family: inherit;
  position: relative;
  align-self: flex-end;
  text-align: center;
  width: 136px;
  height: 32px;
  font-size: 12px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: -0.15px;
  text-transform: uppercase;
  color: #ffffff;
  text-align: left;
  background-color: #ff3e79;
  border-radius: 16px;
  border: none;
  padding-left: 20px; }

.button-action::after {
  content: '';
  width: 10px;
  height: 10px;
  right: 20px;
  top: 10px;
  border-top: 2px solid #ffffff;
  border-right: 2px solid #ffffff;
  position: absolute;
  transform: rotate(45deg); }
