@font-face {
  font-family: TTLakes-Bold;
  src: url(./fonts/TTLakes-Bold.ttf); }

@font-face {
  font-family: TTLakes-Medium;
  src: url(./fonts/TTLakes-Medium.ttf); }

@font-face {
  font-family: TTLakes-Light;
  src: url(./fonts/TTLakes-Regular.ttf); }

.body {
  max-width: 1440px;
  flex-direction: row-reverse;
  margin: 0 auto;
  position: relative;
  font-family: 'TTLakes-Light', Arial, serif;
  transition: all 0.3s;
  overflow-x: hidden; }

.page {
  background-color: #f8f8f8;
  background-size: 100%; }

@media (min-width: 1440px) {
  .body {
    display: flex; } }

.content {
  background-color: #f8f8f8;
  position: relative; }
  .content .content-text {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    width: 100%;
    font-family: "TTLakes-Light", Arial, serif;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #7E7E82;
    text-transform: uppercase;
    font-weight: inherit;
    background-image: linear-gradient(to right, #f8f8f8, #ffffff);
    height: 80px;
    margin: 0;
    margin-bottom: 20px; }

.content::before {
  content: "";
  height: 1px;
  width: 100%;
  position: absolute;
  background-color: #D9FFF5;
  top: 0px;
  left: 0px; }
