@font-face {
  font-family: TTLakes-Bold;
  src: url(./fonts/TTLakes-Bold.ttf); }

@font-face {
  font-family: TTLakes-Medium;
  src: url(./fonts/TTLakes-Medium.ttf); }

@font-face {
  font-family: TTLakes-Light;
  src: url(./fonts/TTLakes-Regular.ttf); }

.body {
  max-width: 1440px;
  flex-direction: row-reverse;
  margin: 0 auto;
  position: relative;
  font-family: 'TTLakes-Light', Arial, serif;
  transition: all 0.3s;
  overflow-x: hidden; }

.page {
  background-color: #f8f8f8;
  background-size: 100%; }

@media (min-width: 1440px) {
  .body {
    display: flex; } }

.footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
  font-size: 14px;
  line-height: 16px;
  color: #7E7E82;
  padding: 20px 32px; }
  .footer p {
    width: 33.3%; }
  .footer .info-text {
    text-align: right; }

.footer::after {
  content: "";
  height: 1px;
  width: 100%;
  position: absolute;
  background-color: #D9FFF5;
  top: 0px;
  left: 0px; }

@media (min-width: 320px) and (max-width: 768px) {
  .footer {
    flex-direction: column;
    text-align: center;
    justify-content: center; }
    .footer p {
      width: 100%; }
    .footer .info-text {
      text-align: center; } }
