@font-face {
  font-family: TTLakes-Bold;
  src: url(./fonts/TTLakes-Bold.ttf); }

@font-face {
  font-family: TTLakes-Medium;
  src: url(./fonts/TTLakes-Medium.ttf); }

@font-face {
  font-family: TTLakes-Light;
  src: url(./fonts/TTLakes-Regular.ttf); }

.body {
  max-width: 1440px;
  flex-direction: row-reverse;
  margin: 0 auto;
  position: relative;
  font-family: 'TTLakes-Light', Arial, serif;
  transition: all 0.3s;
  overflow-x: hidden; }

.page {
  background-color: #f8f8f8;
  background-size: 100%; }

@media (min-width: 1440px) {
  .body {
    display: flex; } }

.card-equipment {
  margin-left: auto;
  margin-right: auto;
  width: 224px;
  height: 160px; }

.card-equipment:before {
  content: '';
  width: 8px;
  height: 8px;
  border-top: 2px solid #ff3e79;
  border-right: 2px solid #ff3e79;
  position: absolute;
  right: 32px;
  bottom: 32px;
  transform: rotate(45deg); }

.card-equipment:after {
  content: '';
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid #ff3e79;
  position: absolute;
  right: 16px;
  bottom: 16px; }

.link-equipment {
  position: absolute;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1B1C21;
  padding: 16px 0 0 16px;
  text-decoration: none; }
