@font-face {
  font-family: TTLakes-Bold;
  src: url(./fonts/TTLakes-Bold.ttf); }

@font-face {
  font-family: TTLakes-Medium;
  src: url(./fonts/TTLakes-Medium.ttf); }

@font-face {
  font-family: TTLakes-Light;
  src: url(./fonts/TTLakes-Regular.ttf); }

.body {
  max-width: 1440px;
  flex-direction: row-reverse;
  margin: 0 auto;
  position: relative;
  font-family: 'TTLakes-Light', Arial, serif;
  transition: all 0.3s;
  overflow-x: hidden; }

.page {
  background-color: #f8f8f8;
  background-size: 100%; }

@media (min-width: 1440px) {
  .body {
    display: flex; } }

.nav {
  overflow-x: scroll; }
  .nav .nav__list {
    display: flex;
    margin: 0;
    padding: 0; }
    .nav .nav__list .nav__item {
      display: inline-block; }
      .nav .nav__list .nav__item .nav__link {
        border-radius: 6px;
        color: #7E7E82;
        display: inline-block;
        font-family: 'TTLakes-Medium', Arial, serif;
        font-size: 16px;
        padding: 6px 22px;
        text-decoration: none;
        white-space: nowrap;
        border: 2px solid rgba(0, 0, 0, 0); }
      .nav .nav__list .nav__item .nav__link:hover,
      .nav .nav__list .nav__item .nav__link:focus {
        border: 2px solid #b8ffec;
        border-radius: 6px;
        background-color: #ffffff; }
      .nav .nav__list .nav__item .nav__link--active {
        border: 2px solid #b8ffec;
        border-radius: 6px;
        background-color: #ffffff; }

.main__nav {
  margin: 24px 0;
  padding-left: 16px; }
