@font-face {
  font-family: TTLakes-Bold;
  src: url(./fonts/TTLakes-Bold.ttf); }

@font-face {
  font-family: TTLakes-Medium;
  src: url(./fonts/TTLakes-Medium.ttf); }

@font-face {
  font-family: TTLakes-Light;
  src: url(./fonts/TTLakes-Regular.ttf); }

.body {
  max-width: 1440px;
  flex-direction: row-reverse;
  margin: 0 auto;
  position: relative;
  font-family: 'TTLakes-Light', Arial, serif;
  transition: all 0.3s;
  overflow-x: hidden; }

.page {
  background-color: #f8f8f8;
  background-size: 100%; }

@media (min-width: 1440px) {
  .body {
    display: flex; } }

.window {
  position: absolute;
  opacity: 0;
  visibility: hidden;
  z-index: 5;
  right: 0;
  top: 0;
  padding-top: 32px;
  width: 440px;
  height: 100%;
  background-color: #ffffff;
  box-shadow: -10px 0 50px 0 rgba(0, 0, 0, 0.1);
  transition: all 0.4s ease; }

.window-open {
  opacity: 1;
  visibility: visible;
  z-index: 5; }

.form__window {
  display: flex;
  flex-direction: column;
  padding: 32px; }

.button__close--window {
  position: absolute;
  top: 32px;
  left: -72px; }

.agreement__letter {
  font-size: 14px;
  line-height: 16px;
  color: #7E7E82;
  margin-bottom: 19px; }
  .agreement__letter .agreement__letter--color {
    color: #ff3e79; }

.input,
.textarea {
  font-family: inherit;
  box-sizing: border-box;
  display: block;
  border: 1px solid #eaeaea;
  border-radius: 8px;
  padding: 12px 16px;
  width: 100%;
  margin-bottom: 16px; }

@media screen and (max-width: 768px) {
  .window {
    width: 320px; }
  .form__window {
    padding: 16px; }
  .button__close--window {
    position: relative;
    left: 16px;
    top: 0;
    margin-bottom: 32px; } }
