@font-face {
  font-family: TTLakes-Bold;
  src: url(./fonts/TTLakes-Bold.ttf); }

@font-face {
  font-family: TTLakes-Medium;
  src: url(./fonts/TTLakes-Medium.ttf); }

@font-face {
  font-family: TTLakes-Light;
  src: url(./fonts/TTLakes-Regular.ttf); }

.body {
  max-width: 1440px;
  flex-direction: row-reverse;
  margin: 0 auto;
  position: relative;
  font-family: 'TTLakes-Light', Arial, serif;
  transition: all 0.3s;
  overflow-x: hidden; }

.page {
  background-color: #f8f8f8;
  background-size: 100%; }

@media (min-width: 1440px) {
  .body {
    display: flex; } }

.commercial {
  margin: 10px 32px; }
  .commercial .commercial-offer {
    text-align: left;
    max-width: 420px;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
    color: #1B1C21;
    margin-bottom: 16px; }
  .commercial .commercial-link {
    display: block;
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #1B1C21;
    position: relative;
    text-decoration: none; }
    .commercial .commercial-link::after {
      content: "";
      width: 10px;
      height: 10px;
      border-top: 2px solid #ff3e79;
      border-right: 2px solid #ff3e79;
      transform: rotate(45deg);
      position: relative;
      right: -10px;
      display: inline-block; }
    .commercial .commercial-link:hover {
      text-decoration: underline; }
