@font-face {
  font-family: TTLakes-Bold;
  src: url(./fonts/TTLakes-Bold.ttf); }

@font-face {
  font-family: TTLakes-Medium;
  src: url(./fonts/TTLakes-Medium.ttf); }

@font-face {
  font-family: TTLakes-Light;
  src: url(./fonts/TTLakes-Regular.ttf); }

.body {
  max-width: 1440px;
  flex-direction: row-reverse;
  margin: 0 auto;
  position: relative;
  font-family: 'TTLakes-Light', Arial, serif;
  transition: all 0.3s;
  overflow-x: hidden; }

.page {
  background-color: #f8f8f8;
  background-size: 100%; }

@media (min-width: 1440px) {
  .body {
    display: flex; } }

.status {
  position: relative;
  padding: 0;
  margin-left: 20px; }

.status::before {
  content: '';
  position: absolute;
  width: 2px;
  height: 32px;
  top: 4px;
  left: -20px;
  background-color: #eaeaea; }

.status .status__list {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0; }

.status .status__item {
  display: flex; }

.button--repair {
  background-image: url(./static/repair.svg);
  background-repeat: no-repeat;
  background-position: center;
  margin-right: 16px; }

.button--checkstatus {
  background-image: url(./static/checkstatus.svg);
  background-repeat: no-repeat;
  background-position: center; }
