@font-face {
  font-family: TTLakes-Bold;
  src: url(./fonts/TTLakes-Bold.ttf); }

@font-face {
  font-family: TTLakes-Medium;
  src: url(./fonts/TTLakes-Medium.ttf); }

@font-face {
  font-family: TTLakes-Light;
  src: url(./fonts/TTLakes-Regular.ttf); }

.body {
  max-width: 1440px;
  flex-direction: row-reverse;
  margin: 0 auto;
  position: relative;
  font-family: 'TTLakes-Light', Arial, serif;
  transition: all 0.3s;
  overflow-x: hidden; }

.page {
  background-color: #f8f8f8;
  background-size: 100%; }

@media (min-width: 1440px) {
  .body {
    display: flex; } }

.link__menu:focus {
  outline: none; }

.list__menu {
  margin: 0;
  padding: 0; }

.link__menu {
  display: inline-block;
  font-family: "TTLakes-Medium", Arial, sans-serif;
  font-size: 17px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1B1C21;
  position: relative;
  margin-bottom: 32px; }

.link:hover {
  color: #7E7E82;
  transition-property: all;
  transition-duration: 0.7s; }

.link__menu:focus {
  color: #7E7E82;
  transition-property: all;
  transition-duration: 0.7s; }

.link__menu:hover::before,
.link__menu:focus::before {
  transition-duration: 0.7s;
  content: "";
  position: absolute;
  width: 4px;
  height: 32px;
  background-color: #41F6D7;
  border-radius: 0px 20px 20px 0px;
  left: -24px;
  top: -4px; }

.link__menu--active {
  color: #7E7E82;
  transition-property: all;
  transition-duration: 0.7s; }

.link__menu--active:before {
  transition-duration: 0.7s;
  content: "";
  position: absolute;
  width: 4px;
  height: 32px;
  background-color: #41F6D7;
  border-radius: 0px 20px 20px 0px;
  left: -24px;
  top: -4px; }
