@font-face {
  font-family: TTLakes-Bold;
  src: url(./fonts/TTLakes-Bold.ttf); }

@font-face {
  font-family: TTLakes-Medium;
  src: url(./fonts/TTLakes-Medium.ttf); }

@font-face {
  font-family: TTLakes-Light;
  src: url(./fonts/TTLakes-Regular.ttf); }

.body {
  max-width: 1440px;
  flex-direction: row-reverse;
  margin: 0 auto;
  position: relative;
  font-family: 'TTLakes-Light', Arial, serif;
  transition: all 0.3s;
  overflow-x: hidden; }

.page {
  background-color: #f8f8f8;
  background-size: 100%; }

@media (min-width: 1440px) {
  .body {
    display: flex; } }

.header {
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  background-color: #ffffff;
  padding: 0 16px 0 16px;
  height: 88px; }
  .header .header__menu {
    margin-right: 20px;
    position: relative; }
  .header .header__logo {
    flex-grow: 1;
    margin-left: 16px; }

.header::before {
  content: '';
  height: 1px;
  width: 100%;
  position: absolute;
  background-color: #D9FFF5;
  top: 87px;
  left: 0px; }

.header__menu::after {
  content: '';
  position: absolute;
  width: 2px;
  height: 32px;
  top: 4px;
  right: -20px;
  background-color: #eaeaea; }

.button--menu {
  background-image: url(./static/burger.svg);
  background-position: center;
  background-repeat: no-repeat; }
